@import url('resources/fonts/dejavu.css');

a:link, a:visited {
	text-decoration: none;
	color: #4A6782;
}

body {
  font-family: 'DejaVu Sans', Arial, Helvetica, sans-serif;
  font-size: 14px;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

li {
	display: list-item;
	text-align: -webkit-match-parent;
}

ul {
	list-style-type: disc;
}

ul.nav-list {
	margin: 0 25px 0 0;
	padding: 0;
}

ul.nav-list li {
	list-style: none;
	float: left;
	padding: 5px 6px;
	text-transform: uppercase;
}

.about-language {
	float: right;
	padding: 0 21px 8px 8px;
	font-size: 11px;
	margin-top: -9px;
	height: 2.9em;
}

.about-language div {
	margin-top: 14px;
}

.flex-header {
	flex: 0 0 auto;
}

.legal-copy {
    margin-left:.5em;
}

.nav-bar-cell1-rev {
	background-color: #F8981D;
	color: #253441;
	margin: auto 5px;
}

.skip-nav {
	position: absolute;
	top: auto;
	left: -9999px;
	overflow: hidden;
}

.sub-nav {
	background-color: #dee3e9;
	float: left;
	width: 100%;
	overflow: hidden;
	font-size: 12px;
}

.top-nav {
	background-color: #4D7A97;
	color: #FFFFFF;
	float: left;
	padding: 0;
	width: 100%;
	clear: right;
	min-height: 2.8em;
	padding-top: 10px;
	overflow: hidden;
	font-size: 12px;
}

.top-nav a:link, .top-nav a:active, .top-nav a:visited {
	color: #FFF;
	text-decoration: none;
	text-transform: uppercase;
}